import React from 'react'
import {definitions} from '../files/definitions.js'
import Typography from '@material-ui/core/Typography'
import '../App.css';

import Aca_call from '../images/locations/aca_callout.svg'
import Aca_loc from '../images/locations/aca_sag.svg'
import Acomm_call from '../images/locations/acomm_callout.svg'
import Acomm_loc from '../images/locations/acomm_cor.svg'
import Btip_call from '../images/locations/btip_callout.svg'
import Btip_loc from '../images/locations/btip_cor.svg'
import Ica_call from '../images/locations/ica_callout.svg'
import Ica_cav_call from '../images/locations/ica_cav_callout.svg'
import Ica_cav_loc from '../images/locations/ica_cav_sag.svg'
import Ica_loc from '../images/locations/ica_sag.svg'
import Mca_call from '../images/locations/mca_callout.svg'
import Mca_loc from '../images/locations/mca_cor.svg'
import Pcomm_call from '../images/locations/pcomm_callout.svg'
import Pcomm_loc from '../images/locations/pcomm_cor.svg'
import Pericol_call from '../images/locations/pericol_callout.svg'
import Pericol_loc from '../images/locations/pericol_sag.svg'
import Gen_call from '../images/locations/gen_callout.svg'
import Gen_loc from '../images/locations/gen_cor.svg'

class LocationImage extends React.Component {

  constructor(props) {
    super(props);
  }

  render() {
    var src = null;
    var location = this.props.location;
    console.log(location)
    if (typeof location === 'undefined') {
      location = -1;
    }
    console.log(location);
    if (this.props.variant === 'callout') {

      switch (parseInt(location)) {
        case 1:
          src = Ica_call;
          break;
        case 2:
          src = Aca_call;
          break;
        case 3:
          src = Acomm_call;
          break;
        case 4:
          src = Pericol_call;
          break;
        case 5:
          src = Mca_call;
          break;
        case 6:
          src = Pcomm_call;
          break;
        case 7:
          src = Btip_call;
          break;
        case 9:
          src = Ica_cav_call;
        default:

          src = Gen_call;

          break;

      }
    } else {
      switch (parseInt(location)) {
        case 1:
          src = Ica_loc
          break;
        case 2:
          src = Aca_loc;
          break;
        case 3:
          src = Acomm_loc;
          break;
        case 4:
          src = Pericol_loc;
          break;
        case 5:
          src = Mca_loc;
          break;
        case 6:
          src = Pcomm_loc;
          break;
        case 7:
          src = Btip_loc
          break;
        case 9:
          src = Ica_cav_loc
        default:
          src = Gen_loc;
          break;
      }

    }
    console.log(src)
    return <img src={src} className={'grid_img'}/>;

  }
}

export default LocationImage;
