import React from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Checkbox from '@material-ui/core/Checkbox';
import NativeSelect from '@material-ui/core/NativeSelect';
import Input from '@material-ui/core/Input';
import Button from '@material-ui/core/Button';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import {withRouter} from 'react-router-dom'
import Divider from '@material-ui/core/Divider'

import '../App.css';

class Landing extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      age: this.props.patient.age,
      lesionLocation: this.props.patient.lesionLocation,
      lesionDiameter: this.props.patient.lesionDiameter,
      htn: this.props.patient.htn,
      ethnicity: this.props.patient.ethnicity,
      hxSAH: this.props.patient.hxSAH
    }
  }

  handleTextChange = name => event => {
    this.setState({[name]: event.target.value});

  };
  handleToggleChange = name => event => {
    this.setState({[name]: event.target.checked});

  };

  urlWithParams(base) {
    var params = "/";
    params = params + this.state.age + '/' + this.state.lesionDiameter + '/' + this.state.lesionLocation + '/' + this.state.ethnicity + '/' + this.state.htn + '/' + this.state.hxSAH;

    var url = '#/' + base + params;
    return url;
  }

  submit() {
    var newPatient = this.props.patient;
    newPatient.age = this.state.age;
    newPatient.lesionDiameter = this.state.lesionDiameter;
    newPatient.lesionLocation = this.state.lesionLocation;
    newPatient.htn = this.state.htn;
    newPatient.ethnicity = this.state.ethnicity;
    newPatient.hxSAH = this.state.hxSAH;

    this.props.updatePatient(newPatient);
  }

  render() {
    return (<div>
      <Grid container={true} justify={'flex-start'} alignContent={'flex-start'} spacing={8} className={'norm_padding'}>

        <Grid item={true} xs={12}>
          <Paper className={'norm_padding'}>
            <Grid container={true} spacing={24} justify={'flex-end'}>
              <Grid item={true} xs={12}>
                <Typography component="h2" variant="h4" gutterBottom={true}>
                  Patient Information
                </Typography>
                <Typography variant="subtitle1" gutterBottom={true}>This information will be used to provide information that is specific to you and your aneurysm.</Typography>
                <Divider/>
              </Grid>
              <Grid item={true} xs={12} sm={6}>
                <TextField required={true} id="age" name="age" label="Age" fullWidth={true} defaultValue={this.state.age != -1
                    ? this.state.age
                    : ""} onChange={this.handleTextChange('age')}/>
              </Grid>
              <Grid item={true} xs={12} sm={6}>
                <TextField required={true} id="lesionDiameter" name="lesionDiameter" label="Aneurysm Diameter or Size in millimeters (mm)" fullWidth={true} defaultValue={this.state.lesionDiameter != -1
                    ? this.state.lesionDiameter
                    : ""} onChange={this.handleTextChange('lesionDiameter')}/>
              </Grid>
              <Grid item={true} xs={12} sm={6}>
                <FormControl >
                  <InputLabel htmlFor="location-native-simple">Aneurysm Location</InputLabel>
                  <NativeSelect fullWidth={true} value={this.state.lesionLocation != -1
                      ? this.state.lesionLocation
                      : ""} input={<Input name = "lesionLocation" id = "lesionLocation" onChange = {
                      this.handleTextChange('lesionLocation')
                    } />}>
                    <option value={-1}/>
                    <option value={1}>Intra-dural Internal Carotid Artery</option>
                    <option value={2}>Anterior Cerebral Artery</option>
                    <option value={3}>Anterior Communicating Artery</option>
                    <option value={4}>Pericollosal Artery</option>
                    <option value={5}>Middle Cerebral Artery</option>
                    <option value={6}>Posterior Communicating Artery</option>
                    <option value={7}>Basilar Tip</option>
                    <option value={8}>Other Posterior Circulation</option>
                    <option value={9}>Cavernous Carotid Artery</option>
                  </NativeSelect>
                </FormControl>
              </Grid>
              <Grid item={true} xs={12} sm={6}>
                <FormControl >
                  <InputLabel htmlFor="ethnicity-native-simple">Ethnicity</InputLabel>
                  <NativeSelect fullWidth={true} value={this.state.ethnicity != -1
                      ? this.state.ethnicity
                      : ""} input={<Input name = "ethnicity" id = "ethnicity" onChange = {
                      this.handleTextChange('ethnicity')
                    } />}>
                    <option value={-1}/>
                    <option value={1}>Finnish</option>
                    <option value={2}>Japanese</option>
                    <option value={0}>Other</option>
                  </NativeSelect>
                </FormControl>
              </Grid>
              <Grid item={true} xs={12} sm={6}>
                <FormControlLabel control={<Checkbox checked = {
                    this.state.htn
                  }
                  onChange = {
                    this.handleToggleChange('htn')
                  }
                  value = {
                    'htn'
                  } />} label="I have high blood pressure."/>
              </Grid>
              <Grid item={true} xs={12} sm={6}>
                <FormControlLabel control={<Checkbox checked = {
                    this.state.hxSAH
                  }
                  onChange = {
                    this.handleToggleChange('hxSAH')
                  }
                  value = {
                    'hxSAH'
                  } />} label="I have had a brain bleed before."/>
              </Grid>
              <Grid item={true} container={'true'} xs={12} direction={'row'} spacing={8}>

                <Grid item={true} xs={12} sm={6}>
                  <Button fullWidth={true} variant={'outlined'} color={'primary'} href={'#/home'}>I do not know.</Button>
                </Grid>
                <Grid item={true} xs={12} sm={6}>
                  <Button fullWidth={true} variant={'contained'} color={'primary'} onClick={() => this.submit()} href={this.urlWithParams('home')} disabled={this.state.age <= 17 || this.state.lesionDiameter == -1 || this.state.lesionLocation == -1 || this.state.ethnicity == -1}>Next</Button>
                  {
                    this.state.age <= 17 || this.state.lesionDiameter == -1 || this.state.lesionLocation == -1 || this.state.ethnicity == -1
                      ? <Typography variant={'caption'} color={'primary'}>Please complete the form before proceeding.</Typography>
                      : null
                  }</Grid>

              </Grid>
            </Grid>

          </Paper>
        </Grid>

      </Grid>
    </div>);
  }
}

export default Landing;
