import React from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Card from '@material-ui/core/Card'
import Paper from '@material-ui/core/Paper'
import Divider from '@material-ui/core/Divider';


import '../App.css';

import {large_threshold} from '../files/constants.js'

//Image imports

import annie_rec_img from '../images/annie_rec.jpg'

class AboutImg extends React.Component {
  constructor(props) {

    super(props);
    this.state = {
      isLarge: window.innerWidth > large_threshold
    }
  }

  componentWillMount() {
    window.addEventListener('resize', () => {
      this.setState({
        isLarge: window.innerWidth > large_threshold
      });
    }, false);
  }

  render() {
    var size_class = this.state.isLarge
      ? 'large'
      : '';
    return (<div>
      <Grid container={true} spacing={16} className={'norm_padding'}>

          <Paper>
            <Grid container={true} spacing={16} className={'norm_padding'} direction={'column'}>
              <Grid className={'content-block summary'} item={true} xs={12} container={true} direction={this.state.isLarge
                  ? 'row-reverse'
                  : 'column'} spacing={16}>

                <Grid item={true} xs={12} sm={6}>
                  <img src={annie_rec_img} className={size_class}/>
                </Grid>
                <Grid item={true} xs={12} sm={6} alignItems={'center'} container={true}>
                  <div className={size_class + ' summary-block-text'}>

              <p className={'supertitle'}>Treatment:</p>
              <Typography variant={'title'} gutterBottom={true}>What you need to know: aneurysm imaging</Typography>
              <Typography variant={'body1'}>There are a number of tests that can be used to look at and measure aneurysms. These tests are used to learn the size, shape, and location of an aneurysm. They can be repeated through time to see whether an aneurysm's size is stable or growing, which is indicator that an aneurysm is more likely to rupture.</Typography>
            </div>
          </Grid>
          </Grid>
          <Divider/>
          <Grid item={true} container={true} direction={'row'} spacing ={16}>
          <Grid item={true} xs={12} sm={4} >
            <Card>

              <CardMedia component="img" image={'https://static1.squarespace.com/static/56f58a5f3c44d8fc598c0590/t/56f60627f699bb496bdad3d1/1458964092711/Philips+Cath+Lab.png?format=1500w'} title="Digital Subtraction Angiography (DSA)"/>
              <CardContent>
                <Typography variant="h5" component="h2">
                  DSA
                </Typography>
                <Typography gutterBottom={true} variant="subtitle2" component="h2">
                  Digital Subtraction Angiography
                </Typography>
                <Typography component="p">
                  <ul>
                    <li>Invasive procedure. Calming medications are given. A catheter is inserted into an artery in the leg or arm and fed through the arteries to the brain. Dye is released and pictures are taken.</li>
                    <li>Most accurate test, especially for small aneurysms.</li>
                    <li>Risks: very low (0.07%) risk of permanent neurological damage.
                    </li>
                    <li>Small radiation exposure.</li>
                  </ul>
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item={true} xs={12} sm={4}>
            <Card>

              <CardMedia component="img" image={'https://www.radiologyinfo.org/gallery-items/images/patient-undergoing-mri-philips6.jpg'} title="Digital Subtraction Angiography (DSA)"/>
              <CardContent>
                <Typography variant="h5" component="h2">
                  MRA
                </Typography>
                <Typography gutterBottom={true} variant="subtitle2" component="h2">
                  Magnetic Resonance Angiography
                </Typography>
                <Typography component="p">
                  <ul>
                    <li>Non-invasive procedure. Head must lay in closed, space for a short time. Dye may be injected during test to highlight blood vessels on images.</li>
                    <li>Accurate test for diagnosis for all but the smallest aneurysms. Used for imaging after treatment.</li>
                    <li>Risks: uncommon allergic reaction to dye.
                    </li>
                    <li>No radiation exposure.</li>
                  </ul>
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item={true} xs={12} sm={4}>
            <Card>
              <CardMedia component="img" image={'http://img.timeinc.net/time/daily/2007/0712/ct_scan_1219.jpg'} title="Digital Subtraction Angiography (DSA)"/>
              <CardContent>
                <Typography variant="h5" component="h2">
                  CTA
                </Typography>
                <Typography gutterBottom={true} variant="subtitle2" component="h2">
                  Computed Tomography Angiography
                </Typography>
                <Typography variant={'body1'}>
                  <ul>
                    <li>Non-invasive procedure. Head must lay in closed, space for a short time. Dye may be injected during test to highlight blood vessels on images.</li>
                    <li>Accurate test for diagnosis of all but the smallest aneurysms. Used for imaging after treatment.</li>
                    <li>Risks: uncommon allergic reaction to dye.</li>
                    <li>Small radiation exposure which may increase the risk for cancer.</li>
                  </ul>
                </Typography>
              </CardContent>

            </Card>
          </Grid>
          </Grid>
        </Grid>
      </Paper>
    </Grid>
  </div>)
  }
}

export default AboutImg;
