import React from 'react';
import {HashRouter as Router, Route, Redirect} from 'react-router-dom';
import DataEntry from './views/data-entry.jsx';
import Scores from './views/scores.jsx';
import Home from './views/home.jsx';
import Landing from './views/landing.jsx'
import Options from './views/options.jsx';
import Options2 from './views/options2.jsx';
import Options3 from './views/options3.jsx';
import Options4 from './views/options4.jsx';
import RiskGraphs from './views/risk-graphs.jsx';
import ImgSlideshow from './views/img-slideshow.jsx';

import AboutSurg from './views/about-surg.jsx';
import AboutImg from './views/about-img.jsx';
import AboutRfm from './views/about-rfm.jsx';

export default class MainRouter extends React.Component {
  constructor(props) {
    super(props)
  }
  render() {
    return (<Router>
      <div>
        <Route path='/data-entry' render={(props) => <DataEntry updatePatient={(pat) => this.props.updatePatient(pat)} patient={this.props.patient}/>}/>
        <Route path='/scores/:age?/:size?/:location?/:ethnicity?/:htn?/:sah?' render={(props) => <Scores patient={this.props.patient}/>}/>
        <Route path='/home/:age?/:size?/:location?/:ethnicity?/:htn?/:sah?' render={(props) => <Home {...props} location={props.location} updatePatient={(pat) => this.props.updatePatient(pat)} patient={this.props.patient}/>}/>
        <Route path='/landing' render={(props) => <Landing updatePatient={(pat) => this.props.updatePatient(pat)} patient={this.props.patient}/>}/>
        <Route path='/options' render={(props) => <Options patient={this.props.patient}/>}/>
        <Route path='/options2' render={(props) => <Options2 patient={this.props.patient}/>}/>
        <Route path='/options3' render={(props) => <Options3 patient={this.props.patient}/>}/>
        <Route path='/options4' render={(props) => <Options4 patient={this.props.patient}/>}/>
        <Route path='/risk-graphs/:age?/:size?/:location?/:ethnicity?/:htn?/:sah?' render={(props) => <RiskGraphs {...props} patient={this.props.patient}/>}/>
        <Route path='/about-rfm' render={(props) => <AboutRfm patient={this.props.patient}/>}/>
        <Route path='/about-surg' render={(props) => <AboutSurg patient={this.props.patient}/>}/>
        <Route path='/about-img' render={(props) => <AboutImg patient={this.props.patient}/>}/>
        <Route path='/img-slideshow' render={(props) => <ImgSlideshow patient={this.props.patient}/>}/>
        <Route exact={true} path='/' render={() => <Redirect to={'/landing'}/>}/>
        <Route exact={true} path='' render={() => <Redirect to={'/landing'}/>}/>

      </div>
    </Router>)
  }
};
