import React, {Component} from 'react';

import MainRouter from './routes.jsx';
import Paper from '@material-ui/core/Paper'
import {MuiThemeProvider, createMuiTheme} from '@material-ui/core/styles'
import Header from './components/Header.jsx'

import {isuia1YSurgicalOutcome, isuia5YRuptureRisk, calculatePhases, ucasYearlyRuptureRate} from './files/constants.js'

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#E64C3C'
    },
    secondary: {
      main: '#90a4ae'
    }
  }
})

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      patient: {
        lesionLocation: -1,
        lesionDiameter: -1,
        age: -1,
        htn: false,
        hxSAH: false,
        ethnicity: -1,
        phases: null,
        isuiaRupt: null,
        isuiaEndo: null,
        ucas: null
      }
    }
    this.updatePatient = this.updatePatient.bind(this);
  }

  updatePatient(updatedPatient) {
    console.log(updatedPatient)
    updatedPatient.phases = calculatePhases(updatedPatient);
    updatedPatient.isuiaRupt = isuia5YRuptureRisk(updatedPatient.lesionLocation, updatedPatient.lesionDiameter, updatedPatient.hxSAH);
    updatedPatient.isuiaEndo = isuia1YSurgicalOutcome(updatedPatient.age, updatedPatient.lesionLocation, updatedPatient.lesionDiameter);
    updatedPatient.ucas = ucasYearlyRuptureRate(updatedPatient.lesionLocation, updatedPatient.lesionDiameter);
    this.setState({patient: updatedPatient})
  }

  render() {
    return (
      <div className={'app_background'}>
      <MuiThemeProvider theme={theme}>
        <Header/>
        <div className={'app_container'}>

          <MainRouter updatePatient={(pat) => this.updatePatient(pat)} patient={this.state.patient}/>
        </div>
      </MuiThemeProvider>
    </div>);
  }
}

export default App;
