import React from 'react'
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogContent from '@material-ui/core/DialogContent';

import getDefinition from '../files/definitions.js'

import '../App.css';

class Definition extends React.Component {
  constructor(props) {

    super(props);
    this.state = {
      content: getDefinition(this.props.word)
    }
  }


  render() {
    return (<div>
      <Dialog open={this.props.open} onClose={() => this.props.onClose()} aria-labelledby="simple-dialog-title">
        <DialogTitle className={'DialogTitle'}>{this.props.word}</DialogTitle>
        <DialogContent>
          <DialogContentText>
          {this.state.content}
          </DialogContentText>
        </DialogContent>
      </Dialog>

    </div>)

  }
}



  export default Definition;
