import React from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import ChevronRight from '@material-ui/icons/ChevronRight'

import {BarChart} from 'react-easy-chart'

import '../App.css';

class RiskGraphs extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      risk_rupture: typeof this.props.match.params.risk !== 'undefined'
        ? this.props.match.params.risk_rupture
        : '',
      risk_endo: typeof this.props.match.params.risk_endo !== 'undefined'
        ? this.props.match.params.risk_endo
        : '',
      risk_cran: typeof this.props.match.params.risk_cran !== 'undefined'
        ? this.props.match.params.risk_cran
        : ''
    }
  }

  componentWillMount() {
    window.scrollTo(0, 0);
  };
  render() {
    return (<div>
      <Grid container={true} justify={'flex-start'} alignContent={'flex-start'} spacing={8} className={'norm_padding'}>
        <Grid ref={'chartContainer1'} item={true} xs={12} className={'content-block'}>
          <p className={'supertitle'}>Treatment:</p>
          <Typography variant={'title'}>Compare Your Options</Typography>
          <BarChart ref={'chart1'} axisLabels={{
              x: 'Choice',
              y: 'Risk'
            }} colorBars="colorBars" grid="grid" width={320} height={300} yDomainRange={[0, 100]} data={[
              {
                x: 'Clipping',
                y: parseInt(this.state.risk_cran)
              }, {
                x: 'Coiling',
                y: parseInt(this.state.risk_endo)
              }, {
                x: 'Rupture with No Treatment',
                y: parseInt(this.state.risk_cran)
              }
            ]}/>
        </Grid>
        <Grid item={true} container={true} justify={'flex-end'} xs={12}>
          <Button href={'#/options'}>OptionsV1
            <ChevronRight/></Button>
          <Button href={'#/options2'}>OptionsV2
            <ChevronRight/></Button>
          <Button href={'#/options3'}>OptionsV3
            <ChevronRight/></Button>
        </Grid>
      </Grid>
    </div>)
  }
}

export default RiskGraphs;
