import React from 'react';

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'

import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import AppBar from '@material-ui/core/AppBar'

import '../App.css';

import aneurysm_img from '../images/aneurysm.png'
import aneurysm_flow_img from '../images/aneurysm_flow.png'
import location_img from '../images/aneurysm_location.png'
import coiling_img from '../images/coiling.svg'
import donothing_img from '../images/donothing.png'
import angio_img from '../images/angio.jpg'

class ImgSlideshow extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tab: 0
    }
  }

  componentWillMount() {
    window.scrollTo(0, 0);
  }

  handleTabChange = (event,value) => {
    console.log(value)
    this.setState({tab: value});
  };



  render() {
    return (<div>
      <Grid container={true} spacing={16} className={'norm_padding'}>
        <Paper>
          <Grid container={true} justify={'flex-start'} alignContent={'flex-start'} spacing={8} className={'norm_padding'}>
            <AppBar position="static" color="default">
              <Tabs value={this.state.tab} onChange={this.handleTabChange} indicatorColor="primary" textColor="primary" variant="scrollable" scrollButtons="auto">
                <Tab label="Aneurysm"/>
                <Tab label="Closeup"/>
                <Tab label="Arteries"/>
                <Tab label="Coiling"/>
              </Tabs>
            </AppBar>
            <div className={'slideshow-container'}>
              {this.state.tab === 0 && <img src={aneurysm_img} className={'slideshow-img'}/>}
              {this.state.tab === 1 && <img src={aneurysm_flow_img} className={'slideshow-img'}/>}
              {this.state.tab === 2 && <img src={location_img} className={'slideshow-img'}/>}
              {this.state.tab === 3 && <img src={coiling_img} className={'slideshow-img'}/>}
            </div>
          </Grid>
        </Paper>
      </Grid>
    </div>)
  }
}

export default ImgSlideshow;
